
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
XHeader,
Toast
},
data() {
return {
noData: false,
list: [],
isloading: false,
curIndex: 0
};
},
mounted() {
this.initData(true);
this.$vux.loading.show({
text: "加载中..."
});
},
methods: {

starActivity(ETID,YMDM,JZDWDM){
this.$router.push({name: "VaccinateDetail", params: {ETID: ETID, YMDM: YMDM ,JZDWDM:JZDWDM}});
},
/**
* @param upOrDown
* true 第一次加载或者刷新
* false 上拉加载
*/
initData(upOrDown) {
var myDate = new Date();
this.start = myDate.getFullYear() + "" + (myDate.getMonth() + 1);
if (upOrDown) {
this.pageIndex = 1;
} else {
this.pageIndex++;
}

this.$ajax
.post("", {
"MSH.1": "preventiveInoculationService",
"MSH.2": "list",
'pageIndex': this.pageIndex,
'pageSize': "10",
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
})
.then(data => {
this.$vux.loading.hide();
if (typeof data != "undefined" && data.length > 0) {

this.list = this.list.concat(data);
} else {
if (this.pageIndex > 1) {
this.$vux.toast.text("没有更多了", "bottom");
this.$refs.scroll.finishPullUp();
} else {
this.noData = true;
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
.catch(error => {
console.log(error);
this.noData = true;
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
});
}
}
};



